import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useHistory, useLocation } from "react-router-dom";
import { toast } from "react-toastify";
import {
  FaCode,
  FaColumns,
  FaExchangeAlt,
  FaFileAlt,
  FaPercent,
  FaPlus,
  FaRemoveFormat,
  FaSync,
  FaTrash,
} from "react-icons/fa";
import {
  Button,
  Form,
  FormGroup,
  FormText,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Label,
} from "reactstrap";
import queryString from "query-string";
import { Formik } from "formik";
import * as Yup from "yup";

import "./SubjectOutputStandardEdit.css";

import { RoutesString } from "pages/routesString";
import subjectGoalAPI from "api/common/subject-goal";
import subjectOutputStandardAPI from "api/common/subject-output-standard";
import Breadcrumbs from "components/admin/Breadcrumbs/Breadcrumbs";
import Notification from "components/common/Notification/Notification";
import LoadingBar from "components/common/LoadingBar/LoadingBar";
import AsteriskIcon from "components/common/AsteriskIcon/AsteriskIcon";
import PageHeader from "./components/PageHeader/PageHeader";
import i18n from "i18n/i18n";

export default function SubjectOutputStandardEdit() {
  const { t } = useTranslation();

  const history = useHistory();

  const [id, setId] = useState(null);
  const [isUpdatePage, setIsUpdatePage] = useState(false);
  const [initialValues, setInitialValues] = useState({
    ma: "",
    mota: "",
    mucDo: "",
    mucTieuMonHoc: ""
  });
  const [notification, setNotification] = useState({
    color: "danger",
    isOpen: false,
    messages: [],
  });
  const [isLoading, setIsLoading] = useState(false);
  const [subjectGoals, setSubjectGoals] = useState([]);

  useEffect(async () => {
    const parsed = queryString.parse(window.location.search);
    if (parsed.id) {
      setId(parsed.id);
      setIsUpdatePage(true);
      updateForm(parsed.id);
    }
    setIsLoading(true);
    try {
      const subjectGoalsRes = await subjectGoalAPI.getAll();
      setSubjectGoals(subjectGoalsRes.contents);
      if (!parsed.id) {
        setInitialValues({
          ...initialValues,
          mucTieuMonHoc: subjectGoalsRes.contents[0].id,
        });
      }
    } catch (err) {
      if (err.response) {
        if (err?.response?.status === 403 || String(err?.response?.message) === 'NOT_OWNER') {
          toast.error(t("RESPONSE_MESSAGE403"));
        }
        else if (err?.response?.status === 401) {
          toast.error(t("RESPONSE_MESSAGE401"));
        } else if (err?.response?.status === 400) {
          toast.error(t("RESPONSE_MESSAGE400"));
        } else if (err?.response?.status === 409) {
          toast.error(t("RESPONSE_MESSAGE409"));
        } else if (err?.response?.status === 404) {
          toast.error(t("RESPONSE_MESSAGE404"));
        } else {
          toast.error(t("RESPONSE_MESSAGE500"));
        }
      }

    } finally {
      setIsLoading(false);
    }
  }, []);

  const updateForm = async function (id) {
    setIsLoading(true);
    try {
      const res = await subjectOutputStandardAPI.getById(id);
      setInitialValues({
        ma: res.ma,
        mota: res.mota,
        mucDo: res.mucDo,
        mucTieuMonHoc: res.mucTieuMonHoc
      });
    } catch (err) {
      if (err.response) {
        if (err?.response?.status === 403 || String(err?.response?.message) === 'NOT_OWNER') {
          toast.error(t("RESPONSE_MESSAGE403"));
        }
        else if (err?.response?.status === 401) {
          toast.error(t("RESPONSE_MESSAGE401"));
        } else if (err?.response?.status === 400) {
          toast.error(t("RESPONSE_MESSAGE400"));
        } else if (err?.response?.status === 409) {
          toast.error(t("RESPONSE_MESSAGE409"));
        } else if (err?.response?.status === 404) {
          toast.error(t("RESPONSE_MESSAGE404"));
        } else {
          toast.error(t("RESPONSE_MESSAGE500"));
        }
      }
    } finally {
      setIsLoading(false);
    }
  };

  const handleEditOnClick = async function (values, isValid) {
    if (!isValid) {
      return;
    }
    if (isUpdatePage) {
      const body = {
        ...values,
        mucTieuMonHoc: parseInt(values.mucTieuMonHoc)
      };
      try {
        const res = await subjectOutputStandardAPI.putById(id, body);
        if (res) {
          setNotification({
            color: "success",
            isOpen: true,
            messages: [t("admin.UPDATE_SUCCESS")],
          });
          updateForm(id);
        }
      } catch (err) {
        if (err?.response) {
          if (err?.response?.status === 403 || String(err?.response?.message) === 'NOT_OWNER') {
            toast.error(t("RESPONSE_MESSAGE403"));
          }
          else if (err?.response?.status === 401) {
            toast.error(t("RESPONSE_MESSAGE401"));
          } else if (err?.response?.status === 400) {
            toast.error(t("RESPONSE_MESSAGE400"));
          } else if (err?.response?.status === 409) {
            toast.error(t("RESPONSE_MESSAGE409"));
          } else if (err?.response?.status === 404) {
            toast.error(t("RESPONSE_MESSAGE404"));
          } else {
            if (String(err?.response?.data?.error)?.includes("ER_DUP_ENTRY")) {
              toast.error(t("ER_DUP_ENTRY"));
            } else if (String(err?.response?.data?.error)?.includes("ID_NOT_FOUND")) {
              toast.error(t("RESPONSE_MESSAGE404"));
            } else {
              toast.error(t("RESPONSE_MESSAGE500"));
            }
          }
        }
      }
    } else {
      const body = {
        ...values,
        mucTieuMonHoc: parseInt(values.mucTieuMonHoc)
      };
      try {
        const res = await subjectOutputStandardAPI.post(body);
        if (res.id) {
          setNotification({
            color: "danger",
            isOpen: false,
            messages: [],
          });
          history.push(
            `${RoutesString.ADMIN_SUBJECT_OUTPUT_STANDARD_EDIT}?id=${res.id}`
          );
        }
      } catch (err) {
        if (err?.response) {
          if (err?.response?.status === 403 || String(err?.response?.message) === 'NOT_OWNER') {
            toast.error(t("RESPONSE_MESSAGE403"));
          }
          else if (err?.response?.status === 401) {
            toast.error(t("RESPONSE_MESSAGE401"));
          } else if (err?.response?.status === 400) {
            toast.error(t("RESPONSE_MESSAGE400"));
          } else if (err?.response?.status === 409) {
            toast.error(t("RESPONSE_MESSAGE409"));
          } else if (err?.response?.status === 404) {
            toast.error(t("RESPONSE_MESSAGE404"));
          } else {
            if (String(err?.response?.data?.error)?.includes("ER_DUP_ENTRY")) {
              toast.error(t("ER_DUP_ENTRY"));
            } else if (String(err?.response?.data?.error)?.includes("ID_NOT_FOUND")) {
              toast.error(t("RESPONSE_MESSAGE404"));
            } else {
              toast.error(t("RESPONSE_MESSAGE500"));
            }
          }
        }
      }
    }
  };

  const handleDeleteOnClick = async function (e) {
    e.preventDefault();
    try {
      const res = await subjectOutputStandardAPI.deleteById(id);
      if (res.status === 200) {
        setNotification({
          color: "success",
          isOpen: true,
          messages: [t("admin.common.DELETED")],
        });
        updateForm(id);
      }
    } catch (err) {
      if (err.response) {
        if (err?.response?.status === 403 || String(err?.response?.message) === 'NOT_OWNER') {
          toast.error(t("RESPONSE_MESSAGE403"));
        }
        else if (err?.response?.status === 401) {
          toast.error(t("RESPONSE_MESSAGE401"));
        } else if (err?.response?.status === 400) {
          toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE400"));
        } else if (err?.response?.status === 409) {
          toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE409"));
        } else if (err?.response?.status === 404) {
          toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE404"));
        } else {
          if (String(err?.response?.data?.error)?.includes("ID_NOT_FOUND")) {
            toast.error(t("RESPONSE_MESSAGE404"));
          } else {
            toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE500"));
          }
        }
      }
    }
  };

  return (
    <div className="page">
      {isLoading && <LoadingBar />}

      <Breadcrumbs
        currentItem={
          isUpdatePage
            ? t(
              "admin.subject_output_standard_edit.UPDATE_SUBJECT_OUTPUT_STANDARD"
            )
            : t(
              "admin.subject_output_standard_edit.ADD_SUBJECT_OUTPUT_STANDARD"
            )
        }
        items={[
          {
            href: RoutesString.ADMIN_SUBJECT_OUTPUT_STANDARD_LIST,
            title: t(
              "admin.subject_output_standard_list.SUBJECT_OUTPUT_STANDARD_LIST"
            ),
          },
        ]}
      />

      <PageHeader
        titleHeader={
          isUpdatePage
            ? t(
              "admin.subject_output_standard_edit.UPDATE_SUBJECT_OUTPUT_STANDARD"
            )
            : t(
              "admin.subject_output_standard_edit.ADD_SUBJECT_OUTPUT_STANDARD"
            )
        }
      />

      <div className="page-content">
        <Formik
          initialValues={initialValues}
          enableReinitialize={true}
          validationSchema={Yup.object().shape({
            ma: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
            mota: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
            mucDo: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
          })}
        >
          {({ values, errors, dirty, isValid, handleChange, handleReset }) => (
            <Form>
              <Notification
                color={notification.color}
                isOpen={notification.isOpen}
                messages={notification.messages}
              />
              <FormGroup className="row">
                <Label for="ma" className="col-lg-3">
                  {t("admin.subject_output_standard_edit.CODE")}{" "}
                  <AsteriskIcon />
                </Label>
                <div className="col-lg-6">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <FaCode />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      type="text"
                      name="ma"
                      placeholder={t(
                        "admin.subject_output_standard_edit.CODE_PLACEHOLDER"
                      )}
                      value={values.ma}
                      onChange={handleChange}
                      invalid={errors.ma}
                      readOnly={isUpdatePage}
                    />
                  </InputGroup>
                  {errors.ma && <FormText color="danger">{errors.ma}</FormText>}
                </div>
              </FormGroup>{" "}
              <br />
              <FormGroup className="row">
                <Label for="mota" className="col-lg-3">
                  {t("admin.subject_output_standard_edit.DESCRIPTION")}{" "}
                  <AsteriskIcon />
                </Label>
                <div className="col-lg-6">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <FaFileAlt />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      type="textarea"
                      name="mota"
                      placeholder={t(
                        "admin.subject_output_standard_edit.DESCRIPTION_PLACEHOLDER"
                      )}
                      value={values.mota}
                      onChange={handleChange}
                      invalid={errors.mota}
                    />
                  </InputGroup>
                  {errors.mota && (
                    <FormText color="danger">{errors.mota}</FormText>
                  )}
                </div>
              </FormGroup>{" "}
              <br />
              <FormGroup className="row">
                <Label for="mucDo" className="col-lg-3">
                  {t("admin.subject_output_standard_edit.LEVEL")}{" "}
                  <AsteriskIcon />
                </Label>
                <div className="col-lg-6">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <FaPercent />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      type="text"
                      name="mucDo"
                      placeholder={t(
                        "admin.subject_output_standard_edit.LEVEL_PLACEHOLDER"
                      )}
                      value={values.mucDo}
                      onChange={handleChange}
                      invalid={errors.mucDo}
                    />
                  </InputGroup>
                  {errors.mucDo && (
                    <FormText color="danger">{errors.mucDo}</FormText>
                  )}
                </div>
              </FormGroup>{" "}
              <br />
              <FormGroup className="row">
                <Label for="mucTieuMonHoc" className="col-lg-3">
                  {t("admin.subject_output_standard_edit.SUBJECT_GOAL")}
                </Label>
                <div className="col-lg-6">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <FaColumns />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      type="select"
                      name="mucTieuMonHoc"
                      value={values.mucTieuMonHoc}
                      onChange={handleChange}
                    >
                      {subjectGoals &&
                        subjectGoals.map((item, index) => {
                          return (
                            <option key={index} value={item.id}>
                              {item.ma}
                            </option>
                          );
                        })}
                    </Input>
                  </InputGroup>
                </div>
              </FormGroup>{" "}
              <br />
              <FormGroup>
                <Button
                  color="success"
                  disabled={isUpdatePage ? !isValid : !(isValid && dirty)}
                  onClick={() =>
                    handleEditOnClick(
                      values,
                      isUpdatePage ? isValid : isValid && dirty
                    )
                  }
                >
                  {isUpdatePage ? (
                    <>
                      <FaExchangeAlt /> {t("admin.common.UPDATE")}
                    </>
                  ) : (
                    <>
                      <FaPlus /> {t("admin.common.ADD")}
                    </>
                  )}
                </Button>
                &emsp;
                {isUpdatePage && (
                  <>
                    <Button onClick={handleReset}>
                      <FaSync /> {t("admin.common.REFRESH")}
                    </Button>
                    &emsp;
                    <Button color="danger" onClick={handleDeleteOnClick}>
                      <FaTrash /> {t("admin.common.DELETE")}
                    </Button>
                  </>
                )}
              </FormGroup>
            </Form>
          )}
        </Formik>
      </div>
    </div>
  );
}
